	module.exports = {"data":{"markdownRemark":{"html":"<p>早朝、私は宗教的精神を促進する映画のようなオリジナルの映画「The Past」を見ました。子供が歌うのが好きだと言うと、司祭の指導のもと、大きな歌手になりたい。後で、車の事故で、サソリは彼の声を失い、再び歌うことができませんでした。子供は一心で神を崇拝し、突然彼の声を回復しました。文化的背景がある。</p>\n<p>「啓示の啓示」に関する記事を書く。タイトルは「武道の創造、戒厳令の栽培」と定義されています。この記事は、あまりにも理論的であることはできません、書くべき非常に良いではない、特定のプロットが必要です。どのように議論と訓練を書く方法、過去に書かれていないトレーニングの問題は、訓練は、理論的な問題ではなく、運用上の問題ではない、偶然のプロセスではなく、説教プロセスです。訓練は、いつでもどこでも見つけられる状況に応じて調整する必要があり、訓練計画は生徒の状況に応じて策定する必要があります。各訓練と議論の後、深い印象はなく、目的もありません。オブジェクトに感情を持たせるために、これは本当の訓練です。</p>\n<p>夕方、私は \"ALIEN\"というビデオを見て、最初のエピソードである中国の \"アモルファス\"に翻訳しました。私は1992年に東京で2回目のエピソードを見たので、最初のエピソードを見るのに興味があります。地球上の商業宇宙船が2000万トン以上の鉱石を銀河に戻し、途中で奇妙な波を受けたというSF映画。宇宙船に運ばれたものが特別な形をしていた。 。エイリアンは、それがすべての人を殺し、女性だけを残すほど強力な凶暴な生き物です。その後、彼女はついに宇宙船から宇宙人を撃った。次のエピソードは、この話に続いて、外人は死んでいない、まだそこにいると言っています。</p>\n<p>そのような映画は西洋諸国で大きな市場を持っています。それが精神の原因であるのか、社会の原因であるのか分かりません。途上国ではそうではないかもしれない。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0015.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0015.md","frontmatter":{"title":"1月23日 日曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0015.md absPath of file >>> MarkdownRemark"}}

